<template>
  <div class="main">
    <!-- 生成海报 -->
    <div class="photo_box">
      <div class="mint-swipe-wrap">
        <mt-swipe
          :auto="0"
          @change="handleChange"
          ref="swipeWrapper"
          :show-indicators="false"
        >
          <mt-swipe-item>
            <img class="swipe-img" src="../assets/images/cover1.gif" />
          </mt-swipe-item>
          <mt-swipe-item>
            <img class="swipe-img" src="../assets/images/cover2.gif" />
          </mt-swipe-item>
          <mt-swipe-item>
            <img class="swipe-img" src="../assets/images/cover3.gif" />
          </mt-swipe-item>
        </mt-swipe>
      </div>
      <img
        class="prev-button"
        @click="prev"
        src="../assets/images/left_btn.png"
      />
      <img
        class="next-button"
        @click="next"
        src="../assets/images/right_btn.png"
      />
      <div class="tip">左右滑动选择模板</div>
      <div class="footer">
        <img
          class="share_btn"
          @click="createPoster"
          src="../assets/images/yq_btn.png"
        />
        <img class="btn_rank" @click="gorank" src="../assets/images/rank.png" />
      </div>
    </div>

    <!-- 目标海报 -->
    <div class="posterHtml" id="posterHtml">
      <div class="header">
        <img
          class="photo"
          src="https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png"
          alt=""
        />
        <div class="content">
          <div class="text1">周面包</div>
          <div class="text2">邀请您一起领福利</div>
          <div class="text2">快和我一起做闪耀的自己</div>
        </div>
      </div>
      <div class="qrcode"><div id="qrcodeImg"></div></div>
      <div class="qrcodetext">
        <div>长按扫码</div>
        <div>领取福利</div>
      </div>
      <img class="cover" :src="curr_url" />
    </div>
    <!-- 分享海报 -->
    <div class="post_box" v-if="cover_url">
      <img class="post_img" :src="cover_url" />
      <p class="post_tip">长按保存图片转发给好友</p>
      <img
        class="colse_btn"
        @click="cover_url = ''"
        src="../assets/images/close_btn.png"
      />
    </div>
  </div>
</template>
<script>
import QRCode from "qrcodejs2";
import html2canvas from "html2canvas";

export default {
  data() {
    return {
      cover_url: "",
      curr_url: require("../assets/images/cover1.png"),
      code_url: `https://varshiya.github.io/#/login/?id=123`,
      user_id: "",
      isShowModal: false,
      users: 5,
    };
  },

  created: function () {
    const id = this.$route.query.id;
    if (id) {
      this.user_id = id;
    }
  },
  mounted: function () {
    this.createQrcode();
  },
  methods: {
    handleChange(index) {
      var index2 = index + 1;
      this.curr_url = require("../assets/images/cover" + index2 + ".png");
    },
    prev: function () {
      this.$refs.swipeWrapper.prev();
    },
    next: function () {
      this.$refs.swipeWrapper.next();
    },

    createQrcode() {
      const qrcodeImgEl = document.getElementById("qrcodeImg");
      qrcodeImgEl.innerHTML = "";
      new QRCode(qrcodeImgEl, {
        text: this.code_url,
        width: 60,
        height: 60,
        colorDark: "#000000",
        colorLight: "#ffffff",
        correctLevel: QRCode.CorrectLevel.L,
      });
    },
    createPoster() {
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      const vm = this;
      const domObj = document.getElementById("posterHtml");
      html2canvas(domObj, {
        useCORS: true,
        allowTaint: false,
        backgroundColor: "transparent",
      }).then(function (canvas) {
        vm.cover_url = canvas.toDataURL("image/png");
      });
    },

    gorank() {
      this.$router.push({ path: "rank", query: { id: 1 } });
    },
    gome() {
      this.$router.push({ path: "rank", query: { id: 2 } });
    },
  },
};
</script>
