import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Rank from '../views/Rank.vue'
import Main from '../views/Main.vue'
import Login from '../views/Login.vue'
import Banner from '../views/Banner.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/banner',
    name: 'Banner',
    component: Banner
  },

  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: '/rank',
    name: 'Rank',
    component: Rank
  },
  {
    path: '/main',
    name: 'Main',
    component: Main
  },

]

const router = new VueRouter({
  mode: 'hash',
  routes
})

export default router
